<script>
import apiOkta from '@/apiOkta'
import api from '@/api'
export default {

    name: 'app',
    data() {
        return {
            activeUser: null,
            posts: [],
            search: '',
            allTags: {},
        }
    },
    computed: {
        filteredList() {
            return this.posts.filter(post => {
                return post.title.toLowerCase().includes(this.search.toLowerCase())
            })
        }
    },
    async created() {
        await this.refreshActiveUser(),
            this.refreshPosts(),
            this.getUser()
    },

    watch: {
        // everytime a route is changed refresh the activeUser
        '$route': 'refreshActiveUser'
    },
    methods: {
        login() {
            this.$auth.loginRedirect()
        },
        async refreshPosts() {
            this.temp = await apiOkta.getUser()
            this.family = await api.getFamily(this.temp.profile.email)
            this.posts = await api.getPosts(this.family.familyid)
            this.allTags = await api.getTags()
        },
        async refreshActiveUser() {
            this.activeUser = await this.$auth.getUser()
        },
        async getUser() {
            this.activeUser = await apiOkta.getUser()
        },
        async updateReadCount(id) {
            await api.updateReadCount(id)
            console.log(id)
        },
        hideModal() {
            this.$refs.modal2.hide()
        },
                    hideModal() {
            this.$refs.modal3.hide()
        },    
        async logout() {
            await this.$auth.logout()
            await this.refreshActiveUser()
            this.$router.push('/')
        }
    }
